x;width:100px;margin-left:1%}.logoText{position:absolute;top:25%;font-family:cursive,sans-serif}header{display:inline-block;width:100%;padding:25px 80px}.toggle-button{width:35px;position:absolute;right:80px;top:40px;display:none}.toggle-button span{display:inline-block;width:100%;height:2px;background-color:#000;margin-bottom:8px}.leftIcons{display:flex;flex-direction:row-reverse;justify-self:flex-end;color:white;margin-top:2.5%;margin-left:105%}.icon1{display:none}.icon2{padding-top:5px;margin-left:10px}@media only screen and (max-width: 1387px){.toggle-button{display:block}.topbarRight{display:none}.icon2{padding-top:5px;margin-left:10px;justify-content:flex-end}.leftIcons{display:flex;flex-direction:row-reverse;justify-content:flex-end;color:white;margin-top:2rem;margin-left:94%}.icon1{display:block;margin-right:10px;margin-left:20px}#toggle{width:40px;height:15px;display:flex;padding:5px;margin-right:20px;margin-top:7px;background-color:#1a202c;display:block;border-radius:1000px;cursor:pointer;box-shadow:0px 5px 20px -10px #000000;transition:background-color 0.2s ease-in;margin-left:10px;margin-right:-20px}#toggle .toggle-inner{width:20px;height:15px;background-color:white;margin-right:20px;border-radius:1000px;transition:margin-left 0.2s ease-in, background-color 0.2s ease-in}#toggle .toggle-inner.toggle-active{margin-left:20px}}

.loadmore{
    cursor:pointer;
    width:15%;
    background-color:white;
    border:none;
    box-shadow:0 2px 5px rgba(0,0,0,16),0 2px 10px 0 black;
    padding: 1vw;
    margin-top:10px;
    margin-bottom: 20px;
    margin-left: 42.5%;
    border-radius: 25px;
    font-weight:600;
    font-size: 1vw;
    color:#262A53;
}
.loadmore:hover{ 
    color:white;
    background-color:#262A53;
}
.container{
    transition: background-color 0.2s ease-in;
    /* border-bottom: none; */
}
@media screen and (max-width:600px) {
    .loadmore{
        font-size: 1.8vw;
    }
   
}

.feed{
    height: 250px;
    width: 80%;
    /* border: 1px solid black; */
    margin-left: 10%;
    margin-top: 50px;
    margin-right: 10%;
    margin-bottom:30px;
    background-color:#ecf0f3;
    box-sizing: border-box;
    border-radius: 15px;
    box-shadow: 14px 14px 20px #cbced1, -14px -14px 20px white;
    transition: background-color 0.2s ease-in;

}


.top{
    margin: 25px;
    padding-top:25px;
}

.profileImg{
    height:200px;
    width:300px;
}

.profileName{
    margin:20px;
    font-size: 25px;
    font-weight: bold;
}
.time{
    font: 15px;
    margin-left:20px;
}

.caption{
    margin: 25px;
}

.link{
    margin-top: 10px;

}
.link a{
    text-decoration: none;
    font-weight: 600;
    color: rgb(131, 6, 104);
}

.img{
    margin: 25px;

}

.icons{
    display: flex;
    flex-direction: row;
    justify-content:space-evenly;
}

.icon{
    height: 30px;
    width: 30px;
    box-sizing: border-box;
    border-radius: 50px;
    box-shadow: 4px 4px 10px #cbced1, -4px -4px 10px white;
    display:flex;
    justify-content: center;
    padding:2.5px;
}
.icon1{
    color: #262A53;
    /* margin-left:30px; */
}

.newsText{
    padding-left: 320px;
    font-size:15px;
}

.newsTitle{
    font-size: 25px;
    font-weight: 600;
}
.lowerPart{
    font-size:18px;
}

.link{
    color: #262A53;
    text-decoration:none;
    transition: all .3s ease-in-out .2s;

    
}
.link:hover{
    transition: all .3s ease-in-out .1s;
    border-bottom: 3px solid #5855f8;
}
.bookmark{
    display:flex;
    justify-content: flex-end;
}
.readMore{
    font-size:1vw;
}
@media screen and (max-width:1387px) {
    .feed{
        width:80%;
        height: auto;
    }
    
    .profileImg{
        height:auto;
        width:100%;
    }

    .newsText{
        padding:10px;
        font-size:18px;
    }
    .readMore{
        font-size:2vw;
    }
}
@media screen and (max-width:600px) {
    .feed{
        width:80%;
        height: auto;
    }
    
    .profileImg{
        height:auto;
        width:100%;
    }

    .newsText{
        padding:5%;
        font-size:2vw;
    }
    .newsTitle
    {
        font-size:5vw;
    }
    .muted{
        font-size:3vw;
    }
    .desc{
        font-size:4vw;
    }
    .readMore{
        font-size:3vw;
    }
    .b{
        font-size:4vw;
    }
}

.footer{
    width:100%;
    height:30px;
    background-color:#262A53;
    text-align:center;
    font-size:20px;
    color:white;
}
@media screen and (max-width:600px) {
    .footer{
        font-size:4vw;
    }
   
}
